.App{
    display: block;
    margin: 0;
    overflow-x: hidden;
    color: #fff;
    font-family: sans-serif;
}
#particles{
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: -1;
}

.social{
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center;
}